import React, { useState, useEffect } from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Checkbox from '@mui/material/Checkbox';
import Box from '@mui/material/Box';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import "../styles/Login.css";




const defaultTheme = createTheme({
  components: {
    MuiCssBaseline: {
      styleOverrides: {
        html: {
          height: '100%',
          width: '100%',
        },
        '#root': {
          height: '100%',
          width: '100%',
        },
        body: {
          height: '100%',
          width: '100%',
          backgroundImage: 'radial-gradient(circle, rgba(63,103,251,1) 0%, rgba(216,252,70,1) 100%)',
          backgroundSize: 'cover',
          backgroundRepeat: 'no-repeat',
          backgroundPosition: 'center',
        },
      },
    },
  },
});




export default function SignIn() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState(null);
  const navigate = useNavigate();
  const [focusedField, setFocusedField] = useState(null);


  useEffect(() => {
    const fetchUser = async () => {
      try {
        const response = JSON.parse(localStorage.getItem('token'));
        if (response && response.user) {
          navigate("/Home");
        }
      } catch (error) {
        console.error('Failed to fetch user', error);
      }
    };

    fetchUser();
  }, [navigate]);

  const handleLogin = async (e) => {
    e.preventDefault();
  
    if (!email || !password) {
      setError('Please fill in both fields');
      return;
    }
  
    try {
      const response = await axios.post('https://gatpandan.bscs.tech/public/api/login', {
        email,
        password
      });

      if (response.status === 200) {
        const data = response.data;
        console.log(data); 

        // Save the token
        const token = data.authorisation.token;
        console.log(token); // Log the token

        // Save the user id and name
        const user = data.authorisation && data.user;
        if (user) {
          const userId = user.id;
          const userName = user.name;
          console.log(userId, userName); // Log the user id and name
        
          localStorage.setItem('token', token);
          localStorage.setItem('userId', userId);
          localStorage.setItem('userName', userName);
        }
        

        navigate('/home'); // Redirect to Home.js
      } else {
        // Error handling
        setError('Failed to login');
        console.error(response.data);
      }
    } catch (error) {
      console.error('Error:', error);
      setError('SOMETHING WRONG CHECK CONSOLE LOG');
    }
  };

  return (
                    <ThemeProvider theme={defaultTheme}>
                    <Container component="div" maxWidth="xs">
                      <CssBaseline />
                      <Box
                        component="form" 
                        onSubmit={handleLogin} 
                        noValidate 
                        sx={{ 
                          mt: 15,
                          transform: 'translate3d(0, 0, 0)',
                          backfaceVisibility: 'hidden',
                          perspective: '1000px',
                          alignItems: 'center',
                          display: 'flex',
                          flexDirection: 'column',
                        }}
                      >
                        <Avatar sx={{ m: 1, bgcolor: 'yellow' }}> <LockOutlinedIcon sx={{ color: 'white' }} />
 </Avatar>

                        <Typography component="h1" variant="h5">
                          Login
                        </Typography>
                        <TextField
                          margin="normal"
                          required
                          fullWidth
                          id="email"
                          label="Email Address"
                          name="email"
                          autoComplete="email"
                          autoFocus
                          value={email}
                          onChange={(e) => setEmail(e.target.value)}
                          InputProps={{
                            style: {
                              transition: 'border 0.3s',
                            },
                          }}
                          InputLabelProps={{
                            style: {
                              transition: 'color 0.3s',
                            },
                          }}
                          focused={focusedField === 'email'}
                          onFocus={() => setFocusedField('email')}
                          sx={{
                            '& .MuiOutlinedInput-root': {
                              '&.Mui-focused fieldset': {
                                borderColor: 'yellow',
                              },
                            },
                          }}
                        />
                        <TextField
                          margin="normal"
                          required
                          fullWidth
                          name="password"
                          label="Password"
                          type="password"
                          id="password"
                          autoComplete="current-password"
                          value={password}
                          onChange={(e) => setPassword(e.target.value)}
                          InputProps={{
                            style: {
                              transition: 'border 0.3s',
                            },
                          }}
                          InputLabelProps={{
                            style: {
                              transition: 'color 0.3s',
                            },
                          }}
                          focused={focusedField === 'password'}
                          onFocus={() => setFocusedField('password')}
                          sx={{
                            '& .MuiOutlinedInput-root': {
                              '&.Mui-focused fieldset': {
                                borderColor: 'yellow',
                              },
                            },
                          }}
                        />
                        <Box sx={{ alignSelf: 'flex-start' }}>
                          <FormControlLabel
                            control={<Checkbox value="remember" color="primary" />}
                            label="Remember me"
                          />
                        </Box>
                        {error && <Typography color="error">{error}</Typography>}
                        <Button
                          type="submit"
                          fullWidth
                          variant="outlined"
                          sx={{ 
                            mt: 3, 
                            mb: 2,
                            transition: 'transform 0.3s',
                            backgroundColor: 'rgba(255, 255, 0, 0.5)',
                            color: 'yellow',
                            '&:hover': {
                              transform: 'scale(1.1)',
                              borderColor: 'rgba(255,240,10,0.8)',
                            },
                          }}
                        >
                          Login
                        </Button>
                      </Box>
                    </Container>
                  </ThemeProvider>
                  
  
  
  );
}
