import React, { useEffect, useState } from 'react';
import axios from 'axios';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import DialogActions from '@material-ui/core/DialogActions';
import TextField from '@material-ui/core/TextField';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import "../styles/API.css";

function API() {
  const token = localStorage.getItem('token');
  const userId = localStorage.getItem('userId'); // Get the user ID from local storage
  
  const [election, setElection] = useState([]);
  const [open, setOpen] = useState(false);
  const [editOpen, setEditOpen] = useState(false);
  const [createOpen, setCreateOpen] = useState(false);
  const [selectedElection, setSelectedElection] = useState(null);
  const [editElection, setEditElection] = useState({});
  const [newElection, setNewElection] = useState({ user_id: userId }); 

  const headers = {
    'Accept': 'application/json',
    'Authorization': 'Bearer ' + token
  };

  // READ: Fetch election data
  const fetchElection = async () => {
    try {
      const result = await axios.get('https://gatpandan.bscs.tech/public/api/elections', { headers });
      setElection(result.data.Elections);
      
    } catch (error) {
      console.error('Failed to fetch election data', error);
    }
  };

  useEffect(() => {
    fetchElection();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  // READ:
  const handleClickOpen = (electionData) => {
    setSelectedElection(electionData);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  // EDIT: DIALOG for UPDATE
  const handleEditOpen = (electionData) => {
    setEditElection(electionData);
    setEditOpen(true);
  };

  const handleEditClose = () => {
    setEditOpen(false);
  };

  const handleInputChange = (event) => {
    setEditElection({
      ...editElection,
      [event.target.name]: event.target.value
    });
  };

  // EDIT
  const handleSave = async () => {
    try {
      await axios.put(`https://gatpandan.bscs.tech/public/api/election/${editElection.id}`, editElection, { headers });
      setEditOpen(false);
      fetchElection(); // Refresh the election data
    } catch (error) {
      console.error('Failed to update election data', error);
    }
  };

  // DELETE
  const handleDelete = async (id) => {
    try {
      await axios.delete(`https://gatpandan.bscs.tech/public/api/election/${id}`, { headers });
      fetchElection(); // Refresh the election data
    } catch (error) {
      console.error('Failed to delete election data', error);
    }
  };

  // CREATE:dialog
  const handleCreateOpen = () => {
    setNewElection({ user_id: userId }); // Reset newElection with the user ID
    setCreateOpen(true);
  };

  const handleCreateClose = () => {
    setCreateOpen(false);
  };

  const handleNewInputChange = (event) => {
    setNewElection({
      ...newElection,
      [event.target.name]: event.target.value
    });
  };

  // CREATE
  const handleCreate = async () => {
    try {
      await axios.post('https://gatpandan.bscs.tech/public/api/election', newElection, { headers });
      setCreateOpen(false);
      fetchElection(); // Refresh the election data
    } catch (error) {
      console.error('Failed to create election data', error);
    }
  };

  return (
    <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-start' }}>
  <TableContainer component={Paper} style={{ width: '79%', margin: 'auto', marginTop: '50px', marginBottom: '50px' }}>
        <Table aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>ID</TableCell>
              <TableCell align="right">Election Code</TableCell>
              <TableCell align="right">Election Description</TableCell>
              <TableCell align="right">Election Status</TableCell>
              <TableCell align="right">Action</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {election.map((row) => (
              <TableRow key={row.id}>
                <TableCell component="th" scope="row">
                  {row.id}
                </TableCell>
                <TableCell align="right">{row.election_code}</TableCell>
                <TableCell align="right">{row.election_description}</TableCell>
                <TableCell align="right">{row.election_status}</TableCell>
                <TableCell align="right">
                  <Button variant="contained" color="primary" onClick={() => handleClickOpen(row)}>
                    READ
                  </Button>
                  <Button variant="contained" style={{ backgroundColor: 'purple', color: 'white' }} onClick={() => handleEditOpen(row)}>
                    EDIT
                  </Button>
                  <Button variant="contained" color="secondary" onClick={() => handleDelete(row.id)}>
                    DELETE
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
        <Button 
            variant="contained" 
                 style={{ backgroundColor: 'green', color: 'white', margin: '20px' }} 
                 onClick={handleCreateOpen}>
                             CREATE
                            </Button>
        </TableContainer>
       
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>Election Details:</DialogTitle>
        <DialogContent>
          {selectedElection && (
            <div>
              <p>ID: {selectedElection.id}</p>
              <p>Election Code: {selectedElection.election_code}</p>
              <p>Election Description: {selectedElection.election_description}</p>
              <p>Election Status: {selectedElection.election_status}</p>
            </div>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Close
          </Button>
        </DialogActions>
      </Dialog>
      <Dialog open={editOpen} onClose={handleEditClose}>
        <DialogTitle>Edit Election</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            name="election_code"
            label="Election Code"
            type="text"
            value={editElection.election_code || ''}
            onChange={handleInputChange}
            fullWidth
          />
          <TextField
            margin="dense"
            name="election_description"
            label="Election Description"
            type="text"
            value={editElection.election_description || ''}
            onChange={handleInputChange}
            fullWidth
          />
          <TextField
            margin="dense"
            name="election_status"
            label="Election Status"
            type="text"
            value={editElection.election_status || ''}
            onChange={handleInputChange}
            fullWidth
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleEditClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleSave} color="primary">
            Save
          </Button>
        </DialogActions>
      </Dialog>
      <Dialog open={createOpen} onClose={handleCreateClose}>
        <DialogTitle>Create Election</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            name="election_code"
            label="Election Code"
            type="text"
            value={newElection.election_code || ''}
            onChange={handleNewInputChange}
            fullWidth
          />
          <TextField
            margin="dense"
            name="election_description"
            label="Election Description"
            type="text"
            value={newElection.election_description || ''}
            onChange={handleNewInputChange}
            fullWidth
          />
          <TextField
            margin="dense"
            name="election_status"
            label="Election Status"
            type="text"
            value={newElection.election_status || ''}
            onChange={handleNewInputChange}
            fullWidth
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCreateClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleCreate} color="primary">
            Save
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default API;
