import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Button, Dialog, DialogTitle, DialogContent, DialogActions, TextField, Select, MenuItem, FormControl, InputLabel } from '@material-ui/core';

function Candidate() {
  const token = localStorage.getItem('token');
  const userId = localStorage.getItem('userId');

  const [Candidates, setCandidates] = useState([]);
  const [position, setPositions] = useState([]);

  const [open, setOpen] = useState(false);
  const [editOpen, setEditOpen] = useState(false);
  const [createOpen, setCreateOpen] = useState(false);
  
  const [selectedCandidate, setSelectedCandidate] = useState(null);
 
  const [editCandidate, setEditCandidate] = useState({ user_id: userId, election_id: '' });
  const [newCandidate, setNewCandidate] = useState({ user_id: userId, election_id: '' });

  const headers = {
    'Accept': 'application/json',
    'Authorization': 'Bearer ' + token
  };

  const fetchCandidates = async () => {
    try {
      const result = await axios.get('https://gatpandan.bscs.tech/public/api/candidates', { headers });
      if (result.data && result.data.Candidate) {
        setCandidates(result.data.Candidate);
      } else {
        console.error('Unexpected structure of API response', result);
      }
    } catch (error) {
      console.error('Failed to fetch Candidates data', error);
    }
  };

  const fetchPositions = async () => {
    try {
      const result = await axios.get('https://gatpandan.bscs.tech/public/api/positions', { headers });
      if (result.data && result.data.Position) {
        setPositions(result.data.Position);
        console.log(result.data.Position);
      } else {
        console.error('Unexpected structure of API response', result);
      }
    } catch (error) {
      console.error('Failed to fetch Positions data', error);
    }
  };
  

  const handleRead = (row) => {
    setSelectedCandidate(row);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleEditOpen = (row) => {
    setEditCandidate(row);
    setEditOpen(true);
  };

  const handleEditClose = () => {
    setEditOpen(false);
  };

  const handleInputChange = (event) => {
    setEditCandidate({
      ...editCandidate,
      [event.target.name]: event.target.value
    });
  };

  const handleSave = async () => {
    try {
      await axios.put(`https://gatpandan.bscs.tech/public/api/candidate/${editCandidate.id}`, editCandidate, { headers });
      setEditOpen(false);
      fetchCandidates();
    } catch (error) {
      console.error('Failed to update candidate data', error);
    }
  };

  const handleDelete = async (id) => {
    try {
      await axios.delete(`https://gatpandan.bscs.tech/public/api/candidate/${editCandidate.id}`, { headers });
      fetchCandidates();
    } catch (error) {
      console.error('Failed to delete position data', error);
    }
  };

  const handleCreateOpen = () => {
    setCreateOpen(true);
  };

  const handleCreateClose = () => {
    setCreateOpen(false);
  };

  const handleCreateInputChange = (event) => {
    setNewCandidate({
      ...newCandidate,
      [event.target.name]: event.target.value.toString()
    });
    console.log("handleCreateInputChange called with: ", event.target.name, event.target.value);
};

  const handleCreate = async () => {
    try {
        await axios.post('https://gatpandan.bscs.tech/public/api/candidate', newCandidate, { headers });
      setCreateOpen(false);
      fetchCandidates();
    } catch (error) {
      console.error('Failed to create Candidate data', error);
    }
  };

  useEffect(() => {
    console.log(newCandidate);
  }, [newCandidate]);
  
  useEffect(() => {
    console.log(editCandidate);
  }, [editCandidate]);
  
  
  useEffect(() => {
    fetchCandidates();
    fetchPositions();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div>
      <TableContainer component={Paper} style={{ width: '79%', margin: 'auto', marginTop: '50px', marginBottom: '50px' }}>
        <Table aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>ID</TableCell>
              <TableCell align="right">First Name</TableCell>
              <TableCell align="right">Last Name</TableCell>
              <TableCell align="right">User ID</TableCell>
              <TableCell align="right">Position ID</TableCell>
              <TableCell align="right">Action</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {Candidates.map((row) => (
              <TableRow key={row.id}>
                <TableCell component="th" scope="row">
                  {row.id}
                </TableCell>
                <TableCell align="right">{row.first_name}</TableCell>
                <TableCell align="right">{row.last_name}</TableCell>
                <TableCell align="right">{row.user_id}</TableCell>
                <TableCell align="right">{row.position_id}</TableCell>
                <TableCell align="right">
                  <Button variant="contained" color="primary" onClick={() => handleRead(row)}>
                    READ
                  </Button>
                  <Button variant="contained" style={{ backgroundColor: 'purple', color: 'white' }} onClick={() => handleEditOpen(row)}>
                    EDIT
                  </Button>
                  <Button variant="contained" color="secondary" onClick={() => handleDelete(row.id)}>
                    DELETE
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
        <Button variant="contained" style={{ backgroundColor: 'green', color: 'white' }} onClick={handleCreateOpen}>
          CREATE
        </Button>
      </TableContainer>
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>Candidate Details:</DialogTitle>
        <DialogContent>
          {selectedCandidate && (
            <div>
              <p>ID: {selectedCandidate.id}</p>
              <p>Position Code: {selectedCandidate.first_name}</p>
              <p>Position Name: {selectedCandidate.last_name}</p>
              <p>User ID: {selectedCandidate.user_id}</p>
              <p>Position ID: {selectedCandidate.position_id}</p>
            </div>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Close
          </Button>
        </DialogActions>
      </Dialog>
      <Dialog open={editOpen} onClose={handleEditClose}>
        <DialogTitle>Edit Candidate:</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            name="first_name"
            label="First Name"
            type="text"
            value={editCandidate.first_name}
            onChange={handleInputChange}
            fullWidth
          />
          <TextField
            margin="dense"
            name="last_name"
            label="Last Name"
            type="text"
            value={editCandidate.last_name}
            onChange={handleInputChange}
            fullWidth
          />
         <FormControl fullWidth> 
  <InputLabel id="edit-position-id-label">Position ID</InputLabel>
  <Select
    labelId="edit-election-id-label"
    name="position_id"
    value={editCandidate.position_id}
    onChange={handleInputChange}
  >
    {position.map((position) => (
      <MenuItem key={position.id} value={position.id}>{position.id}</MenuItem>
    ))}
  </Select>
</FormControl>
          
        </DialogContent>
        <DialogActions>
          <Button onClick={handleEditClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleSave} color="primary">
            Save
          </Button>
        </DialogActions>
      </Dialog>
      
      <Dialog open={createOpen} onClose={handleCreateClose}>
  <DialogTitle>Create Position:</DialogTitle>
  <DialogContent>
    <TextField
      autoFocus
      margin="dense"
      name="first_name"
      label="First Name"
      type="text"
      value={newCandidate.first_name}
      onChange={handleCreateInputChange}
      fullWidth
    />
    <TextField
      margin="dense"
      name="last_name"
      label="Last Name"
      type="text"
      value={newCandidate.last_name}
      onChange={handleCreateInputChange}
      fullWidth
    />
    <FormControl style={{ minWidth: 120 }}> 
      <InputLabel id="edit-position-id-label">Position ID</InputLabel>
      <Select
        labelId="edit-position-id-label"
        name="position_id"
        value={newCandidate.position_id}
        onChange={handleCreateInputChange}
      >
        {position.map((position) => (
          <MenuItem key={position.id} value={position.id}>{position.id}</MenuItem>
        ))}
      </Select>
    </FormControl>
  </DialogContent>
  <DialogActions>
    <Button onClick={handleCreateClose} color="primary">
      Cancel
    </Button>
    <Button onClick={handleCreate} color="primary">
      Create
    </Button>
  </DialogActions>
</Dialog>

    </div>
  );
}

export default Candidate;
