import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, Button, Dialog, DialogTitle, DialogContent, DialogActions, TextField, Select, MenuItem, FormControl, InputLabel } from '@material-ui/core';

function Position() {
  const token = localStorage.getItem('token');
  const userId = localStorage.getItem('userId');

  const [positions, setPositions] = useState([]);
  const [elections, setElections] = useState([]);
  
  const [open, setOpen] = useState(false);
  const [editOpen, setEditOpen] = useState(false);
  const [createOpen, setCreateOpen] = useState(false);
  const [selectedPosition, setSelectedPosition] = useState(null);
  const [editPosition, setEditPosition] = useState({ user_id: userId, election_id: '' });
  const [newPosition, setNewPosition] = useState({ user_id: userId, election_id: '' });

  const headers = {
    'Accept': 'application/json',
    'Authorization': 'Bearer ' + token
  };

  const fetchPositions = async () => {
    try {
      const result = await axios.get('https://gatpandan.bscs.tech/public/api/positions', { headers });
      if (result.data && result.data.Position) {
        setPositions(result.data.Position);
      } else {
        console.error('Unexpected structure of API response', result);
      }
    } catch (error) {
      console.error('Failed to fetch Position data', error);
    }
  };

  const fetchElections = async () => {
    try {
      const result = await axios.get('https://gatpandan.bscs.tech/public/api/elections', { headers });
      if (result.data && result.data.Elections) {
        setElections(result.data.Elections);
        console.log(result.data.Elections);
      } else {
        console.error('Unexpected structure of API response', result);
      }
    } catch (error) {
      console.error('Failed to fetch Election data', error);
    }
  };
  

  const handleRead = (row) => {
    setSelectedPosition(row);
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleEditOpen = (row) => {
    setEditPosition(row);
    setEditOpen(true);
  };

  const handleEditClose = () => {
    setEditOpen(false);
  };

  const handleInputChange = (event) => {
    setEditPosition({
      ...editPosition,
      [event.target.name]: event.target.value
    });
  };

  const handleSave = async () => {
    try {
      await axios.put(`https://gatpandan.bscs.tech/public/api/position/${editPosition.id}`, editPosition, { headers });
      setEditOpen(false);
      fetchPositions();
    } catch (error) {
      console.error('Failed to update position data', error);
    }
  };

  const handleDelete = async (id) => {
    try {
      await axios.delete(`https://gatpandan.bscs.tech/public/api/position/${id}`, { headers });
      fetchPositions();
    } catch (error) {
      console.error('Failed to delete position data', error);
    }
  };

  const handleCreateOpen = () => {
    setCreateOpen(true);
  };

  const handleCreateClose = () => {
    setCreateOpen(false);
  };

  const handleCreateInputChange = (event) => {
    setNewPosition({
      ...newPosition,
      [event.target.name]: event.target.value.toString()
    });
    console.log("handleCreateInputChange called with: ", event.target.name, event.target.value);
};

  const handleCreate = async () => {
    try {
        await axios.post('https://gatpandan.bscs.tech/public/api/position', newPosition, { headers });
      setCreateOpen(false);
      fetchPositions();
    } catch (error) {
      console.error('Failed to create position data', error);
    }
  };

  useEffect(() => {
    console.log(newPosition);
  }, [newPosition]);
  
  useEffect(() => {
    console.log(editPosition);
  }, [editPosition]);
  
  
  useEffect(() => {
    fetchPositions();
    fetchElections();
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div>
      <TableContainer component={Paper} style={{ width: '79%', margin: 'auto', marginTop: '50px', marginBottom: '50px' }}>
        <Table aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>ID</TableCell>
              <TableCell align="right">Position Code</TableCell>
              <TableCell align="right">Position Name</TableCell>
              <TableCell align="right">Election ID</TableCell>
              <TableCell align="right">User ID</TableCell>
              <TableCell align="right">Action</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {positions.map((row) => (
              <TableRow key={row.id}>
                <TableCell component="th" scope="row">
                  {row.id}
                </TableCell>
                <TableCell align="right">{row.position_code}</TableCell>
                <TableCell align="right">{row.position_name}</TableCell>
                <TableCell align="right">{row.election_id}</TableCell>
                <TableCell align="right">{row.user_id}</TableCell>
                <TableCell align="right">
                  <Button variant="contained" color="primary" onClick={() => handleRead(row)}>
                    READ
                  </Button>
                  <Button variant="contained" style={{ backgroundColor: 'purple', color: 'white' }} onClick={() => handleEditOpen(row)}>
                    EDIT
                  </Button>
                  <Button variant="contained" color="secondary" onClick={() => handleDelete(row.id)}>
                    DELETE
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
        <Button variant="contained" style={{ backgroundColor: 'green', color: 'white' }} onClick={handleCreateOpen}>
          CREATE
        </Button>
      </TableContainer>
      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>Position Details:</DialogTitle>
        <DialogContent>
          {selectedPosition && (
            <div>
              <p>ID: {selectedPosition.id}</p>
              <p>Position Code: {selectedPosition.position_code}</p>
              <p>Position Name: {selectedPosition.position_name}</p>
              <p>Election ID: {selectedPosition.election_id}</p>
              <p>User ID: {selectedPosition.user_id}</p>
            </div>
          )}
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Close
          </Button>
        </DialogActions>
      </Dialog>
      <Dialog open={editOpen} onClose={handleEditClose}>
        <DialogTitle>Edit Position:</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            name="position_code"
            label="Position Code"
            type="text"
            value={editPosition.position_code}
            onChange={handleInputChange}
            fullWidth
          />
          <TextField
            margin="dense"
            name="position_name"
            label="Position Name"
            type="text"
            value={editPosition.position_name}
            onChange={handleInputChange}
            fullWidth
          />
           <FormControl style={{ minWidth: 120 }}>  
  <InputLabel id="edit-election-id-label">Election ID</InputLabel>
  <Select
    labelId="edit-election-id-label"
    name="election_id"
    value={editPosition.election_id}
    onChange={handleInputChange}
  >
    {elections.map((elections) => (
      <MenuItem key={elections.id} value={elections.id}>{elections.id}</MenuItem>
    ))}
  </Select>
</FormControl>
          
        </DialogContent>
        <DialogActions>
          <Button onClick={handleEditClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleSave} color="primary">
            Save
          </Button>
        </DialogActions>
      </Dialog>
      <Dialog open={createOpen} onClose={handleCreateClose}>
        <DialogTitle>Create Position:</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            name="position_code"
            label="Position Code"
            type="text"
            value={newPosition.position_code}
            onChange={handleCreateInputChange}
            fullWidth
          />
          <TextField
            margin="dense"
            name="position_name"
            label="Position Name"
            type="text"
            value={newPosition.position_name}
            onChange={handleCreateInputChange}
            fullWidth
          />
     <FormControl style={{ minWidth: 120 }}> 
  <InputLabel id="edit-election-id-label">Election ID</InputLabel>
  <Select
    labelId="edit-election-id-label"
    name="election_id"
    value={newPosition.election_id}
    onChange={handleCreateInputChange}
  >
    {elections.map((Elections) => (
      <MenuItem key={Elections.id} value={Elections.id}>{Elections.id}</MenuItem>
    ))}
  </Select>
</FormControl>


          
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCreateClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleCreate} color="primary">
            Create
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

export default Position;
